.tpl-enterprise-list li {
  position: relative;
  float: left;
  width: 33.3%;
  height: 680px;
  overflow: visible;
}
.tpl-enterprise-list li .text, .tpl-enterprise-list li .image {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    padding: 38% 0;
    overflow: hidden;
}
.tpl-enterprise-list li .text {
    background: #2D5891;
    color: #fff;
}
.tpl-enterprise-list li .text > div {
      position: absolute;
      padding: 15% 20%;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
}
.tpl-enterprise-list li .text > div h4 {
        font-weight: normal;
        font-size: 2rem;
        margin-bottom: 20px;
}
.tpl-enterprise-list li .text > div p {
        margin: 0;
        line-height: 1.8;
        font-size: 1.2rem;
}
.tpl-enterprise-list li .image img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
}
.tpl-enterprise-list li:nth-child(even) .text {
    margin-top: 76%;
}
.tpl-enterprise-list li:nth-child(odd) .image {
    margin-top: 76%;
}
@media only screen and (max-width: 768px) {
.tpl-enterprise-list li {
    float: inherit;
    width: 100%;
    height: auto;
    clear: both;
}
.tpl-enterprise-list li .text, .tpl-enterprise-list li .image {
      position: relative;
}
.tpl-enterprise-list li .text > div {
      padding: 0;
      padding-top: 25%;
      text-align: center;
      vertical-align: middle;
}
.tpl-enterprise-list li .text > div h4 {
        font-size: 1.6rem;
        margin-bottom: 5px;
}
.tpl-enterprise-list li:nth-child(odd) .image {
      margin-top: 0;
}
.tpl-enterprise-list li:nth-child(even) .text {
      margin-top: 0;
}
}
.tpl-feedback-form li {
  min-height: 50px;
}
.tpl-feedback-form li span {
  display: inline-block;
  vertical-align: middle;
}
.tpl-feedback-form li span:first-of-type {
    width: 80px;
    padding-right: 20px;
    text-align: right;
}
.tpl-feedback-form li span:nth-of-type(2) {
    width: calc(100% - 120px);
}
.tpl-feedback-form li span:nth-of-type(2) input {
      width: 100%;
      border: 0;
      border-bottom: 1px solid #C3C3C3;
      background: none;
      padding: 10px 5px;
      color: #2D5891;
}
.tpl-feedback-form li span:nth-of-type(2) input:focus {
        border-color: #2D5891;
}
.tpl-feedback-form li span:nth-of-type(2) input.error {
        border-color: #cd0c25;
}
.tpl-feedback-form .tpl-feedback-mark {
  margin-top: 20px;
}
.tpl-feedback-form .tpl-feedback-mark textarea {
    height: 100px;
    width: 100%;
    resize: none;
    background: none;
    border-color: #C3C3C3;
    padding: 10px;
}
.tpl-feedback-form .tpl-feedback-mark textarea.error {
      border-color: #cd0c25;
}
.tpl-feedback-form .tpl-feedback-code {
  margin-top: 20px;
}
.tpl-feedback-form .tpl-feedback-code span:nth-of-type(2) {
    width: 30%;
}
.tpl-feedback-form .tpl-feedback-code span:nth-of-type(3) {
    vertical-align: bottom;
    height: 40px;
    width: 120px;
}
.tpl-feedback-form .tpl-feedback-code span:nth-of-type(3) img {
      height: 100%;
      width: 100%;
}
.tpl-feedback-form .tpl-feedback-operate {
  margin-top: 20px;
  padding-left: 85px;
}
.tpl-history-wrap:before {
  position: relative;
  margin-left: 50%;
  left: -1px;
  bottom: -1px;
  content: '';
  display: inline-block;
  height: 40px;
  width: 0;
  border-right: 1px dashed #2D5891;
}
.tpl-history-item {
  position: relative;
  margin-top: 30px;
  height: 25px;
  line-height: 25px;
}
.tpl-history-item:nth-child(odd) {
    text-align: left;
}
.tpl-history-item:nth-child(odd) .tpl-history-half {
      padding-right: 5%;
}
.tpl-history-item:nth-child(odd) .tpl-history-half > span {
        float: right;
}
.tpl-history-item:nth-child(even) {
    text-align: right;
}
.tpl-history-item:nth-child(even) .tpl-history-half {
      padding-left: 6%;
}
.tpl-history-item:nth-child(even) .tpl-history-half > span {
        float: left;
}
.tpl-history-item:before {
    margin-top: -48px;
    margin-left: -1px;
    top: 50%;
    left: 50%;
    position: absolute;
    content: '';
    display: inline-block;
    height: 40px;
    width: 1px;
    background: #2D5891;
}
.tpl-history-item:after {
    top: 50%;
    left: 50%;
    margin-left: -8px;
    margin-top: -8px;
    position: absolute;
    content: '';
    display: inline-block;
    height: 16px;
    width: 16px;
    border-radius: 8px;
    border: 1px solid #2D5891;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: background 0.3s;
    transition: background 0.3s;
}
.tpl-history-item:hover:after {
    background: #2D5891;
}
.tpl-history-half {
  display: inline-block;
  width: 50%;
}
.tpl-history-half > span:first-child {
    padding: 0 20px;
    color: #2D5891;
    text-shadow: 0 0 0.5px #2D5891;
}
@media only screen and (max-width: 768px) {
.tpl-history-wrap:before {
    margin-left: 10px;
}
.tpl-history-item {
    white-space: nowrap;
}
.tpl-history-item:before {
      left: 10px;
}
.tpl-history-item:after {
      left: 10px;
}
.tpl-history-item:nth-child(odd) .tpl-history-half {
      padding-right: 0;
      padding-left: 10px;
}
.tpl-history-item:nth-child(odd) .tpl-history-half > span {
        float: left;
}
.tpl-history-item:nth-child(even) .tpl-history-half {
      padding-right: 0;
      padding-left: 10px;
}
.tpl-history-item:nth-child(even) .tpl-history-half > span {
        float: left;
}
.tpl-history-half {
    width: 100%;
}
.tpl-history-half > span:first-child {
      padding: 0 5px 0 20px;
}
}
