/************ 动画 ***********/
/* tab 切换*/
.tab-fade-enter, .tab-fade-leave {
  opacity: 0;
}
.tab-fade-enter-active, .tab-fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}



