
.content-wrap {
  min-height: 700px;
}
.main {
  position: relative;
  overflow: hidden;
  width: 100%;
  min-width: 1080px;
  padding-top: 120px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.main.open {
    padding-top: 270px;
}
@media only screen and (max-width: 768px) {
.main {
    min-width: initial;
    padding-top: 50px !important;
}
}



