
.video-wrap {
  position: relative;
  margin: 0 auto;
  width: 80%;
  padding: 22.5% 0;
}
.video-wrap video {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    background: #000000;
}
.video-wrap .video-mask {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    height: 100%;
    width: 100%;
    overflow: hidden;
    background: rgba(30, 30, 30, 0.6);
    text-align: center;
}
.video-wrap .video-mask a {
      display: inline-block;
      position: relative;
      top: 50%;
      margin-top: -3.4rem;
}
.video-wrap .video-mask .iconfont {
      font-size: 6.8rem;
      color: #fff;
}
.video-wrap .video-img {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
}
.video-wrap .video-img img {
      width: 120%;
}
.video-bottom {
  background: #2D5891;
}
@media only screen and (max-width: 768px) {
.video-bottom {
    height: 100px;
    margin-top: -50px;
}
}
@media only screen and (min-width: 768px) {
.video-bottom {
    height: 160px;
    margin-top: -80px;
}
}
@media only screen and (min-width: 992px) {
.video-bottom {
    height: 160px;
    margin-top: -80px;
}
}
@media only screen and (min-width: 1200px) {
.video-bottom {
    height: 240px;
    margin-top: -120px;
}
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}



