
.weixin-img {
  width: 100%;
  height: auto;
}
.icon-weixin {
  color: #50b674;
}
.head-main {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.head-main * {
    -webkit-transition: inherit;
    transition: inherit;
}
.head-wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 120px;
  padding: 0 60px;
  z-index: 99;
  background: #f7f7f7;
  min-width: 1080px;
}
.head-wrap .logo {
    position: absolute;
    display: inline-block;
    top: 28px;
    left: 60px;
}
.head-wrap .nav {
    margin-left: 294px;
    position: relative;
}
.head-wrap .nav .li1 {
      margin-top: 28px;
      padding-bottom: 12px;
      margin-bottom: 12px;
      border-bottom: 1px solid #C3C3C3;
}
.head-wrap .nav .li1 .info1 {
        display: none;
}
.head-wrap .nav .li1 .info2 .left {
        float: right;
        margin-right: 60px;
}
.head-wrap .nav .li1 .info2 .left .lang {
          margin-left: 30px;
}
.head-wrap .nav .li1 .info2 .right {
        position: absolute;
        right: 0;
        top: -4px;
        color: #2D5891;
}
.head-wrap .nav .li1 .info2 .right .iconfont {
          font-size: 2rem;
}
.head-wrap .nav .li2 {
      min-height: 20px;
      line-height: initial;
}
.head-wrap .nav .menus {
      float: left;
}
.head-wrap .nav .menus a {
        float: left;
        display: block;
        margin-right: 40px;
}
.head-wrap .nav .menus a:last-child {
        margin-right: 0;
}
.head-wrap .nav .menus .active {
        color: #2D5891;
}
.head-wrap .nav .search {
      float: right;
      padding-right: 35px;
}
.head-wrap .nav .search .iconfont {
        position: absolute;
        bottom: -1px;
        right: 0;
        font-size: 2rem;
}
.head-wrap .operate-wrap {
    display: none;
}
.search-wrap {
  position: fixed;
  width: 100%;
  top: 120px;
  z-index: 99;
  background: #f7f7f7;
  height: 0;
  overflow: hidden;
  min-width: 1080px;
}
.search-wrap.visible {
    height: 150px;
}
.search-wrap.visible .search-content {
      opacity: 1;
}
.search-wrap .search-content {
    padding-top: 20px;
    width: 660px;
    margin: 0 auto;
    opacity: 0;
    -webkit-transition: inherit;
    transition: inherit;
}
.search-wrap input {
    float: left;
    height: 50px;
    width: 590px;
    border: 1px solid #c2c2c2;
    margin: 0 auto;
    padding: 0 20px;
}
.search-wrap button {
    float: left;
    background: #2D5891;
    height: 50px;
    width: 70px;
}
.search-wrap button:active {
      background: #204271;
}
.search-wrap button .iconfont {
      color: #fff;
      font-size: 2rem;
}
.shrink .head-wrap {
  height: 80px;
  background: #fff;
  -webkit-box-shadow: 0 1px 15px 1px #C3C3C3;
          box-shadow: 0 1px 15px 1px #C3C3C3;
}
.shrink .head-wrap .logo {
    -webkit-transform: scale(0.63);
            transform: scale(0.63);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    top: 20px;
}
.shrink .head-wrap .nav {
    margin-left: 246px;
}
.shrink .head-wrap .nav .li2 {
      margin-top: 20px;
}
.shrink .head-wrap .nav .menus a {
      line-height: 40px;
      padding: 0 20px;
      margin-right: 20px;
}
.shrink .head-wrap .nav .menus a:last-child {
      margin-right: 0;
}
.shrink .head-wrap .nav .menus .active {
      background: #2D5891;
      color: #fff;
}
.shrink .search-wrap {
  top: 120px;
}
@media only screen and (max-width: 768px) {
.shrink .head-wrap, .head-wrap {
    height: 50px;
    padding: 0 30px;
    background: #f7f7f7;
    min-width: initial;
    -webkit-box-shadow: initial;
            box-shadow: initial;
}
.shrink .head-wrap .logo, .head-wrap .logo {
      -webkit-transform: scale(0.28);
              transform: scale(0.28);
      -webkit-transform-origin: 0 0;
              transform-origin: 0 0;
      left: 30px;
      top: 15px;
}
.shrink .head-wrap .nav, .head-wrap .nav {
      margin-left: -30px;
      margin-right: -30px;
      margin-top: -50px;
      z-index: 3;
      top: -550px;
      opacity: 0;
      -webkit-transition: all 0.8s;
      transition: all 0.8s;
}
.shrink .head-wrap .nav.openMenu, .head-wrap .nav.openMenu {
        top: 0;
        opacity: 1;
}
.shrink .head-wrap .nav .li1, .head-wrap .nav .li1 {
        display: none;
}
.shrink .head-wrap .nav .li2, .head-wrap .nav .li2 {
        margin-top: 50px;
        -webkit-box-shadow: 0 1px 5px 0px #ded7d7;
                box-shadow: 0 1px 5px 0px #ded7d7;
}
.shrink .head-wrap .nav .menus, .head-wrap .nav .menus {
        width: 100%;
        padding-top: 50px;
        padding-bottom: 50px;
        background: #f7f7f7;
}
.shrink .head-wrap .nav .menus a, .head-wrap .nav .menus a {
          line-height: 40px;
          padding: 0;
          margin-right: 0;
          font-size: 1.6rem;
          width: 100%;
          text-align: center;
}
.shrink .head-wrap .nav .menus a:last-child, .head-wrap .nav .menus a:last-child {
          margin-right: 0;
}
.shrink .head-wrap .nav .menus .active, .head-wrap .nav .menus .active {
          background: none;
          color: #2D5891;
}
.shrink .head-wrap .nav .search, .head-wrap .nav .search {
        display: none;
}
.shrink .head-wrap .operate-wrap, .head-wrap .operate-wrap {
      display: block;
      position: absolute;
      top: 10px;
      right: 10px;
      color: #2D5891;
      font-size: 2.4rem;
}
.search-wrap {
    display: none;
}
}
@media only screen and (min-width: 1200px) {
.head-wrap {
    padding: 0 80px;
}
.head-wrap .logo {
      left: 80px;
}
.head-wrap .nav .menus a {
      margin-right: 65px;
}
}
@media only screen and (min-width: 1400px) {
.head-wrap .nav .li1 .info1 {
    float: left;
    display: block;
}
.shrink .head-wrap .logo {
    -webkit-transform: scale(0.63);
            transform: scale(0.63);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    left: 360px;
}
.shrink .head-wrap .nav {
    margin-left: 526px;
}
}
.fade-enter, .fade-leave-to {
  opacity: 0;
  height: 0 !important;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  border-bottom: none !important;
  padding-bottom: 0 !important;
}



