        float: left;
}
.tpl-history-item:before {
    margin-top: -48px;
    margin-left: -1px;
    top: 50%;
    left: 50%;
    position: absolute;
    content: '';
    display: inline-block;
    height: 40px;
    width: 1px;
    background: #2D5891;
}
.tpl-history-item:after {
    top: 50%;
    left: 50%;
    margin-left: -8px;
    margin-top: -8px;
    position: absolute;
    content: '';
    display: inline-block;
    height: 16px;
    width: 16px;
    border-radius: 8px;
    border: 1px solid #2D5891;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: background 0.3s;
    transition: background 0.3s;
}
.tpl-history-item:hover:after {
    background: #2D5891;
}
