
.info-list {
  margin-left: -20px;
  margin-right: -20px;
  margin-bottom: 0;
  margin-top: 0;
}
.info-list .item:nth-child(1) {
    margin-top: 0;
}
.info-list .item:nth-child(2) {
    margin-top: 0;
}
.info-list .item, .info-list .last {
    float: left;
    position: relative;
    width: calc(50% - 40px);
    min-width: 440px;
    margin-top: 40px;
    margin-left: 20px;
    margin-right: 20px;
}
.info-list .item {
    background: #2D5891;
    cursor: pointer;
}
.info-list .item .whole {
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
}
.info-list .item .whole:hover {
        -webkit-transform: translate(-8px, -8px);
                transform: translate(-8px, -8px);
}
.info-list .last {
    background: #fff;
    vertical-align: middle;
    cursor: pointer;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.info-list .last:hover {
      color: #fff;
      background: #2D5891;
}
.info-list .last .content {
      position: relative;
      padding: 14% 0;
}
.info-list .last .content > * {
      position: absolute;
      top: 50%;
}
.info-list .last p {
      width: 100%;
      text-align: center;
      display: inline-block;
      margin: -1rem 0 0 0;
      font-size: 2.4rem;
}
.info-list .last .arrow {
      display: inline-block;
      margin-top: -2.5rem;
      right: 40px;
}
.info-list .last .arrow i.iconfont.icon-jiantou {
        font-size: 4.8rem;
}
.info-list .part {
    float: left;
    width: 50%;
    position: relative;
    padding: 16.5% 0;
    overflow: hidden;
    font-family: "PingFangSC-Light", "PingFang SC", "Helvetica Neue", Helvetica, "Hiragino Sans GB", "Microsoft YaHei", SimSun, sans-serif;
    color: #2D5891;
}
.info-list .part.right {
      background: #fff;
}
.info-list .part.right > div {
        padding: 20px;
        bottom: 0;
}
.info-list .part > * {
      position: absolute;
      top: 0;
      left: 0;
}
.info-list .part .p {
      font-size: 1.6rem;
}
.info-list .part img {
      width: 100%;
}
.info-list-move {
  display: none;
  position: relative;
  padding: 16% 0;
}
.info-list-move > .el-carousel__container {
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
}
.info-list-move .item, .info-list-move .last {
    width: 100%;
}
.info-list-move .item {
    background: #2D5891;
}
.info-list-move .item .part {
      float: left;
      width: 50%;
      position: relative;
      padding: 16% 0;
      overflow: hidden;
      font-family: "PingFangSC-Light", "PingFang SC", "Helvetica Neue", Helvetica, "Hiragino Sans GB", "Microsoft YaHei", SimSun, sans-serif;
      color: #2D5891;
}
.info-list-move .item .part.right {
        background: #fff;
        font-size: 1.2rem;
}
.info-list-move .item .part.right > div {
          padding: 20px;
          bottom: 0;
}
.info-list-move .item .part > * {
        position: absolute;
        top: 0;
        left: 0;
}
.info-list-move .item .part .p {
        margin-top: 15px;
        font-size: 1.2rem;
}
.info-list-move .item .part img {
        width: 100%;
}
.info-list-move .item .part .time {
        font-size: 1.2rem;
}
.info-list-move .last {
    background: #fff;
    vertical-align: middle;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.info-list-move .last:active {
      color: #2D5891;
}
.info-list-move .last .content {
      position: relative;
      padding: 16% 0;
}
.info-list-move .last .content > * {
      position: absolute;
      top: 50%;
}
.info-list-move .last p {
      width: 100%;
      text-align: center;
      display: inline-block;
      margin: -1rem 0 0 0;
      font-size: 2.4rem;
}
.info-list-move .last .arrow {
      display: inline-block;
      margin-top: -2.5rem;
      right: 40px;
}
.info-list-move .last .arrow i.iconfont.icon-jiantou {
        font-size: 4.8rem;
}
.time {
  color: #2D5891;
}
@media only screen and (max-width: 768px) {
::-webkit-scrollbar {
    display: none;
}
.list-wrap {
    margin-top: -10px;
    padding-top: 10px;
    margin-left: -10px;
    padding-left: 10px;
    overflow-y: scroll;
}
.info-list {
    display: none;
    margin-left: 0;
    margin-right: 0;
}
.info-list .item, .info-list .last {
      width: 100%;
      min-width: initial;
      margin-top: 0;
      margin-left: 0;
      margin-right: 0;
}
.info-list .last .content {
      padding: 16% 0;
}
.info-list .part {
      padding: 16% 0;
}
.info-list .item .whole:hover {
      -webkit-transform: none;
              transform: none;
}
.info-list .item .whole:active {
      -webkit-transform: translate(-8px, -8px);
              transform: translate(-8px, -8px);
}
.info-list .last:hover {
      color: inherit;
      background: #fff;
}
.info-list .last:active {
      color: #fff;
      background: #2D5891;
}
.info-list .part.right {
      font-size: 1.2rem;
}
.info-list .part.right .p {
        font-size: 1.44rem;
}
.info-list-move {
    display: block;
}
}
@media only screen and (min-width: 1200px) {
.info-list .part.right .p {
    font-size: 2.0rem;
}
}
@media only screen and (min-width: 1800px) {
.info-list .part.right .p {
    font-size: 2.4rem;
}
.info-list .part.right > div {
    padding: 60px 40px;
}
.info-list .last .arrow {
    right: 80px;
}
}



