
.banner {
  position: relative;
  padding: 16.7% 0;
  overflow: hidden;
}
.banner > div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.banner .left-arrow, .banner .right-arrow {
    visibility: hidden;
    display: inline-block;
    position: absolute;
    top: 50%;
    z-index: 3;
    margin-top: -3.4rem;
    border: 1px solid #fff;
    width: 6.8rem;
    height: 6.8rem;
    text-align: center;
    color: #fff;
    opacity: 1;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.banner .left-arrow .iconfont, .banner .right-arrow .iconfont {
      font-size: 3.4rem;
      line-height: 6.8rem;
      font-weight: bold;
}
.banner .left-arrow:hover, .banner .right-arrow:hover {
      background: #fff;
      color: #48494c;
      opacity: 0.6;
}
.banner .left-arrow {
    left: 60px;
}
.banner .right-arrow {
    right: 60px;
}
.banner-left {
  position: relative;
  float: left;
  width: 62.5%;
}
.banner-left:hover .left-arrow, .banner-left:hover .right-arrow {
    visibility: visible;
}
.banner-left .img-wrap {
    position: relative;
    padding: 27% 0;
    height: 0;
    overflow: hidden;
}
.banner-left .img-wrap .img {
      position: absolute;
      width: 100%;
      top: 0;
}
.banner-left .img-wrap .el-carousel, .banner-left .img-wrap .el-carousel__container {
      height: 100%;
}
.banner-left .img-wrap img {
      width: 100%;
}
.banner-right {
  position: relative;
  float: left;
  height: 100%;
  width: 37.5%;
  background-color: #2D5891;
  color: #fff;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.banner-right .content {
    position: relative;
    top: -8%;
    height: 100%;
    margin-left: 40px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.banner-right .content h3 {
      width: 100%;
      font-size: 1.4rem;
      font-weight: normal;
      color: #E8B270;
      margin: 0;
      margin-bottom: 1rem;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
}
.banner-right .content h4 {
      width: 100%;
      color: #fff;
      font-weight: normal;
      line-height: 1.5;
      font-size: 2.4rem;
      margin: 0;
      margin-bottom: 1rem;
}
.banner-right .content p {
      width: 100%;
      color: #fff;
}
.banner-right .bottom {
    position: absolute;
    left: 40px;
    bottom: 40px;
}
.banner-right .count {
    display: inline-block;
    margin-top: 5px;
    width: 1rem;
    text-align: center;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.banner-right .count + span {
      margin: 0 10px;
}
.banner-right .dot {
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.count-wrap {
  width: 90px;
}
.bg-6 {
  background: #d5472f;
}
.bg-6 .count, .bg-6 .content h3 {
    color: #ffca13;
}
.bg-6 .dot.active {
    background: #ffca13;
}
.bg-5 {
  background: #00a867;
}
.bg-5 .count, .bg-5 .content h3 {
    color: #ffe41e;
}
.bg-5 .dot.active {
    background: #ffe41e;
}
.bg-4 {
  background: #4A5876;
}
.bg-4 .count, .bg-4 .content h3 {
    color: #fed751;
}
.bg-4 .dot.active {
    background: #fed751;
}
.bg-3 {
  background: #125E98;
}
.bg-3 .count, .bg-3 .content h3 {
    color: #ffca13;
}
.bg-3 .dot.active {
    background: #ffca13;
}
.bg-2 {
  background: #02368C;
}
.bg-2 .count, .bg-2 .content h3 {
    color: #49c7ff;
}
.bg-2 .dot.active {
    background: #49c7ff;
}
.bg-1 {
  background: #176278;
}
.bg-1 .count, .bg-1 .content h3 {
    color: #64b4ff;
}
.bg-1 .dot.active {
    background: #64b4ff;
}
.move-enter, .move-leave-to {
  opacity: 0;
  left: 80px;
}
.move-enter-to, .move-leave {
  opacity: 1;
  left: 0;
}
.move-enter-active {
  position: relative;
  -webkit-transition: all 1s;
  transition: all 1s;
}
@media only screen and (max-width: 768px) {
.banner {
    padding: 83% 0;
}
.banner .banner-left {
      width: 100%;
      height: 32%;
      overflow: hidden;
}
.banner .banner-left .left-arrow, .banner .banner-left .right-arrow {
        display: none;
}
.banner .banner-right {
      width: 100%;
      height: 68%;
      text-align: center;
}
.banner .banner-right .content {
        position: relative;
        top: -8%;
        margin-left: 0;
        margin-top: 0;
        margin-bottom: 0;
}
.banner .banner-right .content h3 {
          font-size: 1.2rem;
          margin-bottom: 1.2rem;
}
.banner .banner-right .content h4 {
          margin-bottom: 0.5rem;
          font-size: 1.8rem;
}
.banner .banner-right .bottom {
        left: 0;
        bottom: 15%;
        width: 100%;
}
.banner .banner-right .bottom .flex-middle {
          -webkit-box-pack: center;
              -ms-flex-pack: center;
                  justify-content: center;
}
.banner .count-wrap {
      display: none;
}
}
@media only screen and (min-width: 1200px) {
.banner .left-arrow {
    left: 80px;
}
.banner .right-arrow {
    right: 80px;
}
.banner-right .content {
    margin-left: 80px;
}
.banner-right .bottom {
    left: 80px;
}
}



