
.section-wrap {
  position: relative;
}

/* icon bgimages*/
.ic {
  display: inline-block;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.ic.icon-core-1 {
    height: 84px;
    width: 83px;
    background-image: url(../../svg/1.svg);
}
.core-list li:hover .ic.icon-core-1 {
      background-image: url(../../svg/11.svg);
}
.ic.icon-core-2 {
    height: 96px;
    width: 95px;
    background-image: url(../../svg/2.svg);
}
.core-list li:hover .ic.icon-core-2 {
      background-image: url(../../svg/22.svg);
}
.ic.icon-core-3 {
    height: 96px;
    width: 83px;
    background-image: url(../../svg/4.svg);
}
.core-list li:hover .ic.icon-core-3 {
      background-image: url(../../svg/44.svg);
}
.ic.icon-core-4 {
    height: 77px;
    width: 94px;
    background-image: url(../../svg/5.svg);
}
.core-list li:hover .ic.icon-core-4 {
      background-image: url(../../svg/55.svg);
}
.core-logo {
  height: 150px;
  text-align: center;
}
.core-logo .ic {
    vertical-align: top;
}
.core-list {
  position: relative;
  margin: 0 auto;
  -webkit-transition: left 0.3s;
  transition: left 0.3s;
}
.core-list li {
    display: inline-block;
    margin: 1px;
    padding: 40px;
    width: calc(25% - 10px);
    white-space: normal;
    cursor: pointer;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    vertical-align: top;
    height: 390px;
    text-align: left;
}
.core-list li h3 {
      font-size: 1.8rem;
      font-weight: normal;
      -webkit-transition: color 0.3s;
      transition: color 0.3s;
      min-height: 30px;
}
.core-list li p {
      margin-top: 10px;
      line-height: 1.5;
      font-size: 1.4rem;
}
.core-list li:hover {
      -webkit-box-shadow: 0 0 0 1px #2D5891;
              box-shadow: 0 0 0 1px #2D5891;
      background: #fff;
}
.core-list li:hover h3, .core-list li:hover p {
        color: #2D5891;
}
@media only screen and (max-width: 768px) {
.core-list {
    margin: 0;
    white-space: normal;
}
.core-list li {
      margin: 0;
      margin-right: 30px;
      display: block;
      padding: 20px;
      margin-top: 20px;
      width: auto;
      height: auto;
      background: #fff;
      -webkit-box-shadow: 1px 1px 3px 0 #C3C3C3;
              box-shadow: 1px 1px 3px 0 #C3C3C3;
}
.core-list li:hover {
        -webkit-box-shadow: initial;
                box-shadow: initial;
}
.core-list li p {
        margin-top: 15px;
        margin-bottom: 0;
        font-size: 1.2rem;
}
.core-list li h3 {
        font-size: 1.6rem;
        color: #2D5891;
        min-height: initial;
}
.core-content {
    display: inline-block;
    width: calc(100% - 70px);
    vertical-align: middle;
}
.core-logo {
    display: inline-block;
    width: 70px;
    height: 60px;
    overflow: hidden;
    vertical-align: middle;
}
.core-logo .ic {
      background-size: 60% 60%;
}
}
@media only screen and (min-width: 768px) and (max-width: 1420px) {
.core-list li h3 {
    text-align: center;
}
}



