
.article-wrap {
  position: relative;
  display: inline-block;
  padding-left: 50px;
  vertical-align: top;
  width: calc(100% - 360px);
  min-height: 600px;
}
.article-main {
  margin-top: 3.6%;
  margin-bottom: 3.6%;
  margin-right: 25%;
  color: #666666;
  font-size: 1.4rem;
  line-height: 1.7;
  margin-right: 25%;
  min-width: 700px;
}
.article-main p {
    margin: 3.7% 0;
}
.article-main img {
    width: 100%;
    height: auto;
}
.article-list {
  width: 965px;
}
.article-list > .fn-clear {
    padding-top: 20px;
}
.article-title {
  font-size: 3.6rem;
  font-weight: normal;
  color: #000000;
  margin-top: 20px;
  margin-bottom: 20px;
}
.article-title > span {
    display: inline-block;
    vertical-align: middle;
}
.article-menu-name {
  font-size: 18px;
  padding: 10px 30px;
  margin-right: 30px;
  background: #2D5891;
  color: #fff;
}
.article-crumbs {
  position: absolute;
  top: 40px;
  right: 60px;
}
.article-crumbs li {
    display: inline-block;
    color: #666666;
}
.article-crumbs li:after {
      font-family: 'iconfont';
      content: '\e65e';
      margin-left: 5px;
      margin-right: 3px;
      font-size: 1.2rem;
}
.article-crumbs li:last-child {
      color: #2D5891;
}
.article-crumbs li:last-child:after {
        display: none;
}
@media only screen and (max-width: 768px) {
.article-title {
    font-size: 2.4rem;
    margin-bottom: 10px;
}
.article-subtitle {
    font-size: 1.2rem;
}
.article-menu-name {
    display: block;
    margin-bottom: 5px;
    padding: 8px 15px;
    font-size: 1.4rem;
}
.article-wrap {
    padding: 0 30px;
    margin: 0;
    margin-top: 30px;
    width: 100%;
}
.article-main {
    margin-top: 20px;
    min-width: initial;
    margin-right: 0;
}
.article-main img {
      display: inline-block;
      width: 100%;
      height: auto;
}
.article-list {
    width: initial;
    min-width: initial;
    margin-right: 0;
}
.article-crumbs {
    display: none;
}
}
@media only screen and (min-width: 768px) {
.less .article-wrap {
    padding: 3.6% 60px;
    width: 100%;
}
.less .article-header {
    text-align: center;
}
.less .article-crumbs {
    display: none;
}
.less .article-main {
    margin: 0 auto;
    margin-top: 3.6%;
    width: 65%;
    max-width: 760px;
}
.less .article-title {
    margin-top: 0;
    text-align: center;
}
.less .article-list {
    margin: 0 auto;
    width: 65%;
    min-width: 960px;
}
}
@media only screen and (min-width: 1200px) {
.article-wrap {
    width: calc(100% - 380px);
}
.article-crumbs {
    right: 80px;
}
.less .article-wrap {
    padding: 3.6% 80px;
}
}



