
.map-wrap {
  font-size: 0;
  margin-top: 40px;
}
.map-wrap img {
    width: 100%;
    height: auto;
}
@media only screen and (max-width: 768px) {
.map-wrap {
    margin-top: 30px;
}
}
@media only screen and (min-width: 1200px) {
.map-wrap {
    margin-top: 80px;
}
}



